.modal-form {
    display: flex;
    flex-direction: column;

&__user-data {
    display: block;
    text-align: center;
    margin-bottom: 20px;
    @include font-Titan-One (42, 36);
    color: var(--title-color);
  }
  &__field {
    @include font-DM-Sans (24, 36);
    letter-spacing: 0.03em;
    color:var(--subtitle-color);
  }
  &__input-wrapper {
    position: relative;
    margin-top: 4px;
    margin-bottom: 10px;
  }
  &__btn{
    margin-top: 20px;
    letter-spacing: 0.04em;
  }
}

.modal-form__icon {
position: absolute;
top: 50%;
left: 15px;
transform: translateY(-50%);
transition: fill 250ms var(--cubic);
fill: var(--title-color);

.modal-form__input:focus +&{
    fill: var(--title-color);
  }
}

.modal-form__input{
width: 100%;
height: 40px;
padding-left: 46px;
transition: border-color 250ms var(--cubic);

border: 1px solid rgba(44, 226, 89, 0.2);
border-radius: 4px;
color: var(--title-color);

&:focus{
    outline: none;
    border-color:var(--subtitle-color);
}
}